<template>
  <div class="note__item">
    <div class="note__top">
      <h3 class="note__title">{{ note.title }}</h3>
      <p class="note__date">{{ note.date }}</p>
    </div>
    <p class="note__desc">{{ note.desc }}</p>
    <div class="note__btns">
      <button class="note__btn redact" @click="$emit('changeNote', note.id)">
        <img src="@/assets/img/pen.svg" alt="" />
        <span class="note__btn-span">{{words.editbtn[lang]}}</span>
      </button>
      <button class="note__btn delete" @click="$emit('delNote', note.id)">
        <img src="@/assets/img/trash.svg" alt="" />
        <span class="note__btn-span">{{words.deledit[lang]}}</span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "NoteItemTodo",
  props: {
    note: {
      typeof: Object,
      default: {
        title: "asdasd",
        desc: "asdasd",
        id: "asd",
        date: "asdasd",
      },
    },
    lang: String,
  },
  inject: ["words"],
  data() {
    return {};
  },
};
</script>

