<template>
  <div class="changer">
    <div class="container changer__wrap">
      <h3 class="all__notes" v-if="!search">
        {{ notes.length > 0 ? words.infobar[lang] : words.noinfobar[lang] }}
      </h3>
      <h3 class="all__notes" v-else>{{ words.appbarseacrch[lang] }}</h3>
      <button class="ch__btn">
        <div class="ch__cont" v-if="!changer" @click="changerClass">
          <img src="@/assets/img/flex.svg" alt="" />
          <span class="btn__span">{{ words.list[lang] }}</span>
        </div>
        <div class="ch__cont" v-else @click="changerClass">
          <img src="@/assets/img/grid.svg" alt="" />
          <span class="btn__span">{{ words.grid[lang] }}</span>
        </div>
      </button>
    </div>
    <div class="container">
      <div :class="[!changer ? grid : flex]">
        <Note
          v-for="(note, index) in notes"
          :key="index"
          :note="note"
          @changeNote="$emit('changeNote', note.id)"
          @delNote="$emit('delNote', note.id)"
          :lang="lang"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Note from "@/components/NoteItem.vue";
export default {
  name: "NotesTodo",
  components: {
    Note,
  },
  props: {
    notes: {
      typeof: Array,
    },
    search: String,
    lang: String,
  },
  inject: ["words"],
  data() {
    return {
      flex: "changer__flex",
      grid: "changer__grid",
      changer: false,
    };
  },
  methods: {
    changerClass() {
      this.changer = !this.changer;
    },
  },
};
</script>

