<template>
  <header class="header">
    <div class="header__notes">
      <button class="header__lang" v-if="lang == 'ru'" @click="changeLang">
        UZ
      </button>
      <button class="header__lang" v-else @click="changeLang">RU</button>
      <div class="container">
        <div class="header__change">
          <Transition name="opp">
            <p class="header__p" v-if="header == false">
              {{ words.appbartitle[lang] }}
            </p>
            <input
              type="text"
              class="header__input"
              v-model="search"
              :placeholder="words.appbarseacrch[lang]"
              v-else
            />
          </Transition>
        </div>
      </div>
      <button class="header__search" @click="changeHeader">
        <img src="@/assets/img/search.svg" alt="" v-if="header == false" />
        <img src="@/assets/img/close.svg" alt="" v-else />
      </button>
    </div>
  </header>
</template>

<script>
export default {
  name: "NavbarTodo",
  props: {
    lang: String,
  },
  inject: ["words"],
  data() {
    return {
      search: "",
      header: false,
    };
  },
  watch: {
    search(val) {
      this.$emit("getSearch", val);
    },
  },
  methods: {
    changeHeader() {
      this.header = !this.header;
    },
    changeLang() {
      this.$emit("changeLang", this.lang == "ru" ? "uz" : "ru");
    },
  },
};
</script>

