<template>
  <Transition name="show">
    <div class="modal" @click="closeModal">
      <div class="modal__content" @click.stop="">
        <h3 class="modal__title">
          {{ !edit ? words.titlewindow[lang] : words.titlewindowedit[lang] }}
        </h3>
        <div class="modal__inputs">
          <div class="modal__input-cont">
            <span class="modal__input-cont_span">Title</span>
            <input
              type="text"
              class="modal__input"
              placeholder="Title"
              v-model="title"
            />
          </div>
          <div class="modal__input-cont">
            <span class="modal__input-cont_span">Content</span>
            <input
              type="text"
              class="modal__input"
              placeholder="Content"
              v-model="desc"
            />
          </div>
        </div>
        <div class="modal__btns">
          <button class="modal__btn delete" @click="closeModal">
            {{ words.closebtn[lang] }}
          </button>
          <button class="modal__btn redact" @click="addNote" v-if="!edit">
            {{ words.addbtn[lang] }}
          </button>
          <button class="modal__btn redact" @click="editedNote" v-else>
            {{ words.editwindowbtn[lang] }}
          </button>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script>
export default {
  name: "ModalTodo",
  props: {
    edit: Boolean,
    currentId: Number,
    editNote: Object,
    lang: String,
  },
  inject: ['words'],
  data() {
    return {
      title: "",
      desc: "",
      id: this.currentId,
    };
  },
  methods: {
    closeModal() {
      this.$emit("closeModal", false);
      this.title = "";
      this.desc = "";
    },
    addNote() {
      if (this.title != "" && this.desc != "") {
        const item = {
          id: this.id++,
          title: this.title,
          desc: this.desc,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("addNote", item);
        this.title = "";
        this.desc = "";
        this.closeModal();
      }
    },
    editedNote() {
      if (this.title != "" && this.desc != "") {
        const changeNote = {
          id: this.editNote.id,
          title: this.title,
          desc: this.desc,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("editedNote", changeNote);
        this.closeModal();
        this.title = "";
        this.desc = "";
      }
    },
  },
};
</script>

